<template>
    <div class="invitation">
        <nav-bar
            ref="nav-bar"
            class="title-bar"
            title="邀请好友">
            <span
                class="rule-btn"
                @click="setDialog('rule')">活动规则</span>
        </nav-bar>
        <van-swipe
            class="swipe"
            v-if="baseInfo.invitationCarousel && baseInfo.invitationCarousel.length"
            :autoplay="3000"
            :show-indicators="false"
            :vertical="true">
            <van-swipe-item
                class="swipe-item"
                v-for="(item, i) in baseInfo.invitationCarousel"
                :key="i">
                {{ item.user }}邀请了{{ item.discipleNum }}位好友，预计获得{{ item.discipleNum * 4 }}元现金
            </van-swipe-item>
        </van-swipe>
        <div class="content">
            <div
                class="invite-btn scale-infinite"
                @click="quickShare('0')"/>
            <div
                v-if="baseInfo.invitationCode"
                class="code Hairline Hairline_box">我的邀请码
                <span class="number">{{ baseInfo.invitationCode }}</span>
                <span
                    class="copy"
                    @click="copyCode">点击复制</span>
            </div>
            <div
                class="income"
                ref="friends-list"
                v-if="discipleList.length">
                <h2>我的邀请收益</h2>
                <div class="income-title">
                    <div class="user">好友姓名</div>
                    <div class="day">当前阶段</div>
                    <div class="money">当前收益</div>
                </div>
                <van-list
                    v-model="loading"
                    :finished="finished"
                    @load="getMyfriends"
                >
                    <div
                        class="disciple"
                        v-for="(disciple, i) in discipleList"
                        :key="i">
                        <div class="user">
                            {{ disciple.discipleInfo.discipleUserInfo.nickname }}
                        </div>

                        <div
                            v-if="disciple.taskInfo.status !== 1"
                            class="day">
                            第<span>{{ disciple.taskInfo.currentDay }}</span>天</div>
                        <div
                            v-else
                            class="day">
                            <span>第{{ disciple.taskInfo.currentDay }}天未完成</span>
                        </div>

                        <div class="money"><span>
                            ￥{{ (disciple.taskInfo.currentAward/100) }}
                        </span></div>

                        <div><span
                            @click="setDialog(disciple.taskInfo.status === 1
                            ? 'friendsRemind' : '')"
                            :class="{
                                action: true,
                                active: disciple.taskInfo.status === 1,
                        }">{{ disciple.taskInfo.statusText }}</span></div>
                    </div>
                </van-list>
            </div>
            <div class="lucky">
                <h2>如何快速获得奖励</h2>
                <div class="way">
                    <div><p>点击按钮</p><p>分享给好友</p></div>
                    <div><p>好友下载</p><p>安装想看</p></div>
                    <div><p>好友登录阅读</p><p>各得4元</p></div>
                </div>
                <h2>我的奖励</h2>
                <div
                    class="task"
                    v-if="baseInfo.taskDescription.awardCashList.length">
                    <div
                        v-for="(task, i) in baseInfo.taskDescription.awardCashList"
                        :key="i">
                        第{{ day[i] }}天
                        <p class="task-text">{{ task }}<i>元</i></p>
                    </div>
                    <div>首次邀请<p class="task-text">1<i>元</i></p></div>
                </div>
                <p>{{ baseInfo.taskDescription.taskDesc }}</p>
            </div>
            <div
                class="income"
                v-if="discipleList.length === 0">
                <h2>我的邀请收益</h2>
                <p>暂无收益</p>
                <div
                    class="income__btn"
                    @click="quickShare('1')">马上邀请</div>
            </div>
            <div class="tips">
                <h2>财运锦囊</h2>
                <p
                    v-for="(text, i) in baseInfo.invitationTips"
                    :key="i">{{ `${i + 1}.${text}` }}</p>
                <div class="teach"><b @click="watchVideo()">观看视频教程领100金币></b></div>
            </div>
        </div>
        <share-bar
            v-if="channels.length"
            :channels="channels"
            class="share-bar"
            @share="share"/>
        <gray-mask :show="moreInvite">
            <div class="more-invite">
                <div
                    class="more-invite__close"
                    @click="closeMoreInvite"/>
                <div
                    class="more-invite-btn"
                    @click="share('wechat', true)">
                    （{{ moreInviteCount }}/3）
                </div>
            </div>
        </gray-mask>
        <gray-mask
            :show="showVideo"
            class="video-container">
            <div class="teach-video-title">
                <div
                    class="teach-video-title__back"
                    @click="setShowVideo(false)" />
                邀请码视频教程
            </div>
            <mi-video
                class="teach-video"
                :video-url="baseInfo.config.videoUrl"
                :poster="baseInfo.config.videoPoster"
                :duration="0"
                :c-s-style="config.video.CSStyle"
            />
        </gray-mask>
        <invi-dialog
            v-show="dialog === 'successNotice'"
            title="邀请成功通知"
            :content="baseInfo.inviteSuccessPopText"
            btn-text="立即查看"
            @btnClick="toFriendsList('successNotice')"
            @close="setDialog('')"
        />
        <invi-dialog
            v-show="dialog === 'remindNotice'"
            title="好友提醒通知"
            :content="baseInfo.inviteRemindPopText"
            btn-text="立即查看及提醒"
            @btnClick="toFriendsList('remindNotice')"
            @close="setDialog('')"
        />
        <invi-dialog
            v-show="dialog === 'friendsRemind'"
            title="好友提醒"
            :content="baseInfo.inviteRemindPopTextSpecial"
            @close="setDialog('')">
            <share-bar
                :channels="['wechat', 'qq']"
                class="friends-share"
                @share="wakeUp"/>
        </invi-dialog>
        <invi-dialog
            class="rule"
            v-show="dialog === 'rule'"
            title="活动规则"
            @close="setDialog('')">
            <div class="rule-container">
                <div ref="rule-content">
                    <p
                        class="rule-row"
                        v-for="(rule, i) in baseInfo.ruleDescription"
                        :key="i">{{ i + 1 }}.{{ rule }}</p>
                    <p class="rule-bottom">最终解释权归想看所有</p>
                </div>
            </div>
        </invi-dialog>
    </div>
</template>

<script>
import TouchMove from '@/utils/touchmove';
import NavBar from '@/components/NavBar';
import MiVideo from '@/components/MiVideo';
import Dialog from '@/components/Dialog';
import GrayMask from '@/components/GrayMask';
import {
    invitationActivity,
    invitationDisciple,
    tutorialAward,
} from '@/api/invitation';
import {
    toast,
    doLogin,
    isTakeOverBackByWeb,
    copyText,
    getUserAccountInfo,
    setSwipeBackStatus,
} from '@/utils/jsApiV2/client';
import {
    shareInvitationCode,
    shareWithSocialType,
} from '@/utils/jsApiV2/act';
import { sset, set, get } from '@/utils/cache';
import { format } from '@/utils/time';
import defaultConfig from '@/config';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { shareCodeTextToWechat } from '@/utils/share';
import InviDialog from './components/invi-dialog';
import ShareBar from './components/share-bar';
import * as config from './config';

export default {
    name: 'Invite',
    components: {
        NavBar,
        ShareBar,
        InviDialog,
        Dialog,
        GrayMask,
        MiVideo,
    },
    data() {
        return {
            page: 1,
            config,
            swipeLIst: [],
            steps: [],
            dialog: '', // remindNotice、 successNotice、friendsRemind、rule(提醒通知、邀请成通知、好友提醒、规则)
            baseInfo: {
                shareInfos: [],
                taskDescription: {
                    awardCashList: [],
                },
                myFriendsDefaultText: [],
                config: {
                    videoUrl: '',
                    videoPoster: '',
                },
            },
            discipleList: [],
            loading: true,
            finished: false,
            isTrackRemindBtn: false, // 去提醒按钮邀请曝光一次,此变量标记是否已上报
            rootConfig: defaultConfig,
            day: ['一', '二', '三', '四', '五', '六', '七'],
            moreInvite: false, // 多次邀请弹窗显示状态
            moreInviteCount: 1,
            showVideo: false,
            isFirstOpenRule: true,
        };
    },
    computed: {
        channels() {
            const channels = this.baseInfo.shareInfos.map(v => v.type);
            if (channels.length) {
                return [channels[0], 'face', 'sms', ...channels.slice(1)];
            }
            return [];
        },
    },
    created() {
        invitationActivity({}).then((res) => {
            if (res.data.status === 200) {
                this.baseInfo = res.data.data;
                if (this.baseInfo.inviteSuccessPopText) {
                    this.setDialog('successNotice');
                } else if (this.baseInfo.inviteRemindPopText) {
                    this.setDialog('remindNotice');
                }
                sset('shareInfo', JSON.stringify(this.baseInfo.shareInfos));
                this.$o2o({
                    e_c: '邀请码',
                    e_a: '曝光',
                    e_n: '邀请码详情页曝光',
                    e_x: {
                        login: this.baseInfo.invitationCode ? 1 : 0,
                    },
                });
            } else {
                this.$error();
            }
            this.$root.hidePlaceholder();
        }).catch(() => {
            this.$error();
            this.$root.hidePlaceholder();
        });
        this.$root.hidePlaceholder();
        this.getMyfriends();
        // 监听back事件
        addCustomEventListener('backKey', this.backKeyHandler);
        this.userInfo = getUserAccountInfo();
        this.moreInviteCount = get('lastMoreInviteday') === format(Date.now(), 'YY-MM-DD') ? 4 : 1;
    },
    destroyed() {
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backKeyHandler);
    },
    methods: {
        setShowVideo(val) {
            this.showVideo = val;
            isTakeOverBackByWeb(val);
            if (val) {
                this.$o2o({
                    e_c: '邀请码',
                    e_a: '点击',
                    e_n: '观看视频教程',
                });
            }
            setSwipeBackStatus(val ? 'false' : 'true');
        },
        watchVideo() {
            this.setShowVideo(true);
            tutorialAward().then(() => {
                toast('100金币到账成功');
            });
        },
        quickShare(source) {
            this.$o2o({
                e_c: '邀请码',
                e_a: '点击',
                e_n: '立即邀请按钮点击',
                e_x: {
                    position: source,
                },
            });
            this.share('wechat');
        },
        share(channel, isMoreInvite) {
            // 未登录
            if (!this.baseInfo.invitationCode) {
                doLogin('location.reload');
                return;
            }
            // 非多次邀请弹窗的微信邀请
            if (this.moreInviteCount === 1 && !isMoreInvite && channel === 'wechat') {
                setTimeout(() => {
                    this.moreInvite = true;

                    // 打开邀请弹窗时需要先关闭其他弹窗
                    this.setDialog('');

                    // 有弹窗接管时需接管back键
                    isTakeOverBackByWeb(true);

                    // 弹窗第一次弹，需要再cache中更新最后一天弹的日期(弹窗一天只弹一次)
                    set('lastMoreInviteday', format(Date.now(), 'YY-MM-DD'));
                    this.$o2o({
                        e_c: '邀请码',
                        e_a: '曝光',
                        e_n: '微信分享引导弹窗',
                        e_x: { count: 1 },
                    });
                }, 1000);
            }

            // 来源：多次邀请弹窗
            if (isMoreInvite) {
                this.$o2o({
                    e_c: '邀请码',
                    e_a: '点击',
                    e_n: '微信分享引导弹窗',
                    e_x: {
                        count: this.moreInviteCount,
                    },
                });
                // 弹窗三次后自动消失
                if (this.moreInviteCount === 3) {
                    // 移除back键接管
                    isTakeOverBackByWeb(false);
                    this.moreInvite = false;
                }
                // 分享后需要次数加1
                setTimeout(() => {
                    this.moreInviteCount += 1;
                    if (this.moreInviteCount < 4) {
                        this.$o2o({
                            e_c: '邀请码',
                            e_a: '曝光',
                            e_n: '微信分享引导弹窗',
                            e_x: {
                                count: this.moreInviteCount,
                            },
                        });
                    }
                }, 1000);
            }

            // 面对面邀请
            if (channel === 'face') {
                this.$router.push({
                    name: 'invitationFace',
                    query: {
                        shareId: this.baseInfo.invitationCode,
                    },
                });
                return;
            }
            // 短信邀请
            if (channel === 'sms') {
                this.$router.push({
                    name: 'SmsInvite',
                    query: {
                        shareId: this.baseInfo.invitationCode,
                    },
                });
                return;
            }
            // 图片分享
            const shareInfo = this.baseInfo.shareInfos.filter(v => v.type === channel);
            if (shareInfo.length) {
                if (channel === 'wechat') {
                    const url = shareInfo[0].shareUrl;
                    shareCodeTextToWechat(`${url}&share=wechat`, this.baseInfo.invitationCode);
                } else {
                    shareInvitationCode(
                        shareInfo[0].title,
                        shareInfo[0].shareUrl,
                        shareInfo[0].summary,
                        shareInfo[0].imageUrl,
                        config.shareMap[channel][0],
                    );
                }
            }
        },
        copyCode() {
            this.$o2o({
                e_c: '邀请码',
                e_a: '点击',
                e_n: '邀请码复制点击',
                e_x: {
                    page: '邀请码详情页',
                },
            });
            // 报错返回false, 成功返回undefined
            if (copyText(this.baseInfo.invitationCode) !== false) {
                toast('复制邀请码成功');
            } else {
                toast('复制邀请码失败');
            }
        },
        setDialog(dialog) {
            this.dialog = dialog;
            isTakeOverBackByWeb(dialog !== '');
            if (dialog) {
                document.body.addEventListener('touchmove', this.stopSlide, { passive: false });
                if (config.dialogTrackMap[dialog]) {
                    this.$o2o({
                        e_c: '邀请码弹窗',
                        e_a: '曝光',
                        e_n: config.dialogTrackMap[dialog],
                    });
                }
                if (dialog === 'friendsRemind') {
                    this.$o2o({
                        e_c: '邀请码',
                        e_a: '点击',
                        e_n: '去提醒按钮',
                    });
                } else if (dialog === 'rule') {
                    this.$o2o({
                        e_c: '邀请码',
                        e_a: '点击',
                        e_n: '活动规则按钮',
                    });
                }
                if (dialog === 'rule' && this.isFirstOpenRule) {
                    this.$nextTick(this.addListenerWheel);
                    this.isFirstOpenRule = false;
                }
            } else {
                document.body.removeEventListener('touchmove', this.stopSlide, { passive: false });
            }
        },
        stopSlide(e) {
            e.preventDefault();
        },
        wakeUp(channel) {
            const socialType = config.shareMap[channel][0];
            const url = `${location.origin}/mobile-v2/act/invitation/rouse?shareId=${this.baseInfo.invitationCode}`;
            const shareData = {
                thumbUrl: defaultConfig.default.rectLogo,
                title: this.baseInfo.remindText.title,
                text: this.baseInfo.remindText.oldVersionDesc,
                url,
                socialType,
            };
            this.$o2o({
                e_c: '邀请码弹窗',
                e_a: '点击',
                e_n: '好友提醒分享弹窗',
                e_x: {
                    platform: config.shareMap[channel][3],
                },
            });
            // 微信新版用分享文字
            if (socialType === 2) {
                try {
                    window.miui.shareText(`${this.baseInfo.remindText.newVersionDesc}${url}`, socialType);
                } catch (_) {
                    shareWithSocialType(shareData);
                }
            } else {
                shareWithSocialType(shareData);
            }
            this.setDialog('');
        },
        toFriendsList(name) {
            this.$o2o({
                e_c: '邀请码弹窗',
                e_a: '点击',
                e_n: config.dialogTrackMap[name],
            });
            window.scrollTo(0, this.$refs['friends-list'].offsetTop - this.$refs['nav-bar'].offsetHeight);
            this.setDialog('');
        },
        getMyfriends() {
            this.loading = true;
            invitationDisciple({
                page: this.page,
            }).then((res) => {
                if (res.data.status === 200) {
                    this.page += 1;

                    this.discipleList = this.discipleList.concat(res.data.data.discipleList);

                    // 显示的总数等于接口中的总数，加载完成
                    this.finished = this.discipleList.length === res.data.data.discipleCount;

                    if (!this.isTrackRemindBtn &&
                        res.data.data.discipleList.some(v => v.taskInfo.status === 1)) {
                        this.$o2o({
                            e_c: '邀请码',
                            e_a: '曝光',
                            e_n: '去提醒按钮',
                        });
                    }
                    this.loading = false;
                } else {
                    this.$error();
                }
            }).catch((err) => {
                this.finished = true;
                this.loading = false;
                if (err && err.response && err.response.status === 401) {
                    return;
                }
                this.$error();
            });
        },
        closeMoreInvite() {
            this.moreInvite = false;
            // 弹窗只出现一次,关闭时直接将次数设置为4,之后就不会再弹了
            this.moreInviteCount = 4;
            isTakeOverBackByWeb(false);
        },
        backKeyHandler() {
            // 如果有邀请弹窗
            if (this.moreInvite) {
                this.closeMoreInvite();
            } else if (this.showVideo) {
                this.setShowVideo(false);
            } else {
                this.setDialog('');
            }
        },
        addListenerWheel() {
            console.log(this.$refs['rule-content'].clientHeight);
            /* eslint-disable no-new */
            new TouchMove({
                Dom: this.$refs['rule-content'],
                slider: this.$refs['rule-content'],
                moveTopLimit: 0,
                moveBottomLimit: 380 - this.$refs['rule-content'].clientHeight,
                modifyTopLimit: 0,
                modifyBottomLimit: 380 - this.$refs['rule-content'].clientHeight,
            });
        },
    },
};
</script>
