<template>
    <Dialog
        :show.sync="showDialog"
        :outsideClose="true">
        <h3>{{ title }}</h3>
        <p
            v-if="content"
            v-html="content"/>
        <slot><div
            class="btn"
            @click="btnClickHandler">{{ btnText }}</div></slot>
    </Dialog>
</template>
<script>
import Dialog from '@/components/Dialog';

export default {
    name: 'InvitationDialog',
    components: {
        Dialog,
    },
    props: {
        title: {
            type: String,
            required: true,
        },
        content: {
            type: String,
            default: '',
        },
        btnText: {
            type: String,
            default: '立即查看',
        },
    },
    data() {
        return {
            showDialog: true,
        };
    },
    watch: {
        showDialog(v) {
            if (!v) {
                // 关闭弹窗不处理,改由上级处理,
                this.$emit('close', false);
                this.showDialog = true;
            }
        },
    },
    methods: {
        btnClickHandler() {
            this.$emit('btnClick');
        },
    },
};
</script>

