<template>
    <div class="share">
        <div
            v-for="channel in channels"
            :key="channel"
            :class="channel"
            @click="share(channel)">
            {{ config.shareMap[channel][2] }}
        </div>
    </div>
</template>
<script>
import * as config from '../config';

export default {
    name: 'ShareBar',
    props: {
        channels: {
            type: Array,
            default: () => [],
        },
    },
    data() {
        return {
            config,
        };
    },
    methods: {
        share(channel) {
            this.$o2o({
                e_c: '邀请码',
                e_a: '点击',
                e_n: '邀请码分享',
                e_x: {
                    platform: config.shareMap[channel][3],
                },
            });
            this.$emit('share', channel);
        },
    },
};
</script>

