<template>
    <transition name="fade">
        <div
            v-if="show"
            class="dialog"
            @click.self="closeHandler($event, 'outside')">
            <div class="dialog__content">
                <div
                    @click="closeHandler"
                    class="dialog__close" />
                <slot>
                    <div class="dialog__inner">
                        <slot name="icon" />
                        <div class="dialog__msg">
                            <slot name="title"> <h1 v-html='title'/></slot>
                            <slot name="msg"><p v-html="msg" /></slot>
                        </div>
                        <slot name="footer" />
                        <Button
                            :customStyle="{
                                width: '1.9rem',
                                height: '0.43rem',
                                fontSize: '0.16rem',
                            }"
                            @click="clickHandler">{{ btnText }}</Button>
                    </div>
                </slot>
            </div>
        </div>
    </transition>
</template>

<script>
import Button from '@/components/Button';

export default {
    name: 'Dialog',
    components: {
        Button,
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        outsideClose: {
            type: Boolean,
            default: true,
        },
        title: {
            type: String,
            default: '',
        },
        msg: {
            type: String,
            default: '',
        },
        btnText: {
            type: String,
            default: '',
        },
        clickHandler: {
            type: Function,
            default: () => {},
        },
    },
    methods: {
        closeHandler(e, position) {
            if (position === 'outside' && !this.outsideClose) return;

            this.$emit('update:show', false);
        },
    },
};
</script>

<style scoped>
.dialog {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 999;
}

.dialog__content {
    background-color: #fff;
    border-radius: 7px;
    z-index: 1000;
}

.dialog__close {
    width: 30px;
    height: 30px;
    margin: 7px 7px -7px auto;
    background: url(./image/close.png) no-repeat;
    background-size: 12px 12px;
    background-position: center center;
}

.dialog__inner {
    margin: 0 20px 25px 20px;
}

.dialog__msg {
    width: 250px;
    font-size: 15px;
}

.dialog__msg h1 {
    font-size: 18px;
    font-weight: 600;
    line-height: 25px;
    text-align: center;
    margin: 0;
}

.dialog__msg p {
    margin: 19px auto 0 auto;
    line-height: 25px;
    color: rgba(0, 0, 0, 0.8);
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.2s;
}

.fade-enter,
.fade-leave-to {
    opacity: 0;
}
</style>
